body {
    margin: 0;
    padding: 0;
    background-color: black;
    width: 100%;
    height: 100%;
    color: white;
}
.main {
    margin-top: 30%;
    font-family: 'Special Elite', cursive;
    text-align: center;
    font-size: 25px;
}

.in-appear {
    opacity: 0.01;
}

.in-appear.in-appear-active{
    opacity: 2;
    -webkit-transition: opacity 2s ease-in;
    -o-transition: opacity 2s ease-in;
    transition: opacity 2s ease-in;
}
.out-appear {
    opacity: 2;
}

.out-appear.out-appear-active{
    opacity: 0;
    -webkit-transition: opacity 2s ease-out;
    -o-transition: opacity 2s ease-out;
    transition: opacity 2s ease-out;
}
a {
    color: antiquewhite;
    text-decoration: none;
}
a:hover {
    color: white;
    -webkit-box-shadow: 39px 0px 85px 15px rgba(224,252,219,1);
    box-shadow: 39px 0px 85px 15px rgba(224,252,219,1);
}



