body {
    margin: 0;
    padding: 0;
    max-width: 100%;
    max-height: 100%;
}
.gif-bg {
    width: 100%;
    height: 100%;
    z-index: 1;
    position: absolute;
}
.container {
    list-style-type: armenian;
    margin-left: 10%;
    margin-top: 20%;
    position: absolute;
    color: white;
    z-index: 2;
    font-size: 20px;
}
li {
    margin-top: 10%;
    list-style: none;
    font-family: 'Special Elite', cursive;
}
.home {
    padding-left: 25%;
}
.about {
    padding-left: 50%;
}
.donate {
    padding-left: 10%;
}
li:hover {
    cursor: pointer;
}
